const getDefaultFrom = (sourceModel) => {
  return sourceModel ? VALUE_FROM.SOURCE : VALUE_FROM.NONE;
}

const getSourceModelString = (list) => {
  let string = '';
  list.forEach(item => {
    if(string === '') {
      string = item.name
    } else {
      string = `${string}.${item.name}`;
    }
  });
  return string;
}

const getFieldValue = (value) => {
  // if (value?.selected && value.selected?.rawDate) {
  //   return { ...value, selected: { value: value.selected?.rawDate } };
  // }
  return value;
};

const getCustomTarget = (item) => {
  if (item.targetJsonModel) {
    return item.targetJsonModel.id;
  }
  return item.targetModel;
};

const getModelSubField = (fields) => {
  if(fields) {
    const newFields = fields.filter(f => Object.keys(f).length > 0);
    return newFields;
  }
  return fields;
}

const getModelFields = (values) => {
  const list = values.slice(1).map((value) => {
    return { name: value };
  });
  return list;
};

const getFieldInfo = (item) => {
  if (item.target && !isCustomTarget(item.target)) {
    return { ...item, fullName: item.target };
  } else {
    if (item.targetModel) {
      // refers to base model
      return { ...item, fullName: item.targetModel };
    }
    return { ...item, modelType: ModelType.CUSTOM, name: item.jsonTarget };
  }
};

const getContextValue = (value, from) => {
  let contextValue = {};
  let newValue = value;
  if (value && value?.selected && value?.selected?.value) {
    const splitedValue = `${value.selected.value}`.split('.');
    const modelSubField = getModelFields(splitedValue);
    if (from === VALUE_FROM.CONTEXT) {
      const contextModel = { name: upperCaseFirstLetter(splitedValue[0]) };
      return { contextModel };
    }
    if (from === VALUE_FROM.SELF) {
      newValue.selected = { ...newValue.selected, value: splitedValue[0] };
      return { value: newValue };
    }
  }
  return contextValue;
};

const updateBuilderFields = (oldFields, item, fields) => {
  const newFields = oldFields.map((field) => {
    let flag = false;
    if (field.name === item.name) {
      flag = true;
      field.hideFields = false;
      field.fields = [...fields];
    }
    if (field.fields && !flag) {
      field.fields = updateBuilderFields(field.fields, item, fields);
    }
    return { ...field };
  });
  return newFields;
};

const findFields = (item, jsonData = []) => {
  let field = jsonData.find((f) => f.name === item.name);
  if (!field) {
    for (let i = 0; i < jsonData.length; i++) {
      const jsonField = jsonData[i];
      const fields = get(jsonField, 'value.fields');
      if (fields) {
        field = findFields(item, fields);
        if (field) {
          break;
        }
      }
    }
  }
  return field;
};

const getNewBuilderFields = (fields, searchText) => {
  return fields.map(field => {
    let newField = {...field};
    let hasChildrenShow = false;
    if(newField.value && newField.value.fields) {
      const newFieldlist = getNewBuilderFields(newField.value.fields, searchText);
      hasChildrenShow = newFieldlist.findIndex(f => f.isHidden === false) !== -1;
      newField = {
        ...newField,
        value: {
          ...newField.value,
          fields: newFieldlist,
        }
      }
    }
    if((!field.name.toLowerCase().includes(searchText.toLowerCase())) && !hasChildrenShow) {
      newField.isHidden = true;
    } else {
      newField.isHidden = false;
    }
    return newField;
  });
}

const showHideBuilderFields = (oldFields, item, hide) => {
  const newFields = oldFields.map((field) => {
    let flag = false;
    if (field.name === item.name) {
      flag = true;
      field.hideFields = hide;
    }
    if (field.fields && !flag) {
      field.fields = updateBuilderFields(field.fields, item, hide);
    }
    return { ...field };
  });
  return newFields;
};

const clearChildrenParentValue = ({
  fields = [],
  from = VALUE_FROM.PARENT,
  shouldChangeFrom = true,
}) => {
  return fields.map((field) => {
    let newField = { ...field };
    const newFieldFrom = get(newField, 'value.from');
    if (newFieldFrom === from) {
      if (shouldChangeFrom) {
        newField.value.from = VALUE_FROM.NONE;
      }
      if (newField.value && newField.value.selected) {
        newField = Object.assign(
          {},
          {
            ...newField,
            value: {
              ...newField.value,
              selected: {
                ...newField.value.selected,
                value: null,
              },
            },
          }
        );
      }
      newField.modelSubField = [];
    }
    if (newField.value && newField.value.fields) {
      const fields = [
        ...clearChildrenParentValue({
          fields: newField.value.fields,
          from,
          shouldChangeFrom,
        }),
      ];
      newField = Object.assign(
        {},
        {
          ...newField,
          value: {
            ...newField.value,
            fields,
          },
        }
      );
      // newField.value.fields.splice(0, fields.length);
      // clearChildrenParentValue({fields: newField.value.fields, from, shouldChangeFrom});
    }

    return newField;
  });
};

function Builder({ params }) {
  const classes = useStyles();
  const { state, update } = useStore();
  const { builderFields } = state;
  const [builderRecord, setBuilderRecord] = React.useState();
  const [model, setModel] = React.useState();
  const [sourceModel, setSourceModel] = React.useState();
  const [sourceModelList, setSourceModelList] = React.useState([]);

  const [loading, setLoading] = React.useState(false);
  const [showSaveMessage, setShowSaveMessage] = React.useState(null);
  const [newRecord, setNewRecord] = React.useState(false);
  const [modelFieldMap, setModelFieldMap] = React.useState({});
  const [metaFields, setMetaFields] = React.useState([]);
  // const [builderFields, setBuilderFields] = React.useState([]);
  const [errors, setErrors] = React.useState({});
  const [openFieldSelector, setOpenFieldSelector] = React.useState(false);

  const setBuilderFields = React.useCallback(
    (fields) => {
      update((draft) => {
        set(draft, 'builderFields', fields);
      });
    },
    [update]
  );

  const handleRowChange = React.useCallback(
    (row, rowIndex, key, value) => {
      update((draft) => {
        const record = get(draft.builderFields, `${row.dataPath}`);

        if (
          key === 'value.from' &&
          [VALUE_FROM.CONTEXT, VALUE_FROM.SELF].includes(get(record, 'value.from'))
        ) {
          const prevFields = get(record, 'value.fields', []);
          if (prevFields.length) {
            const fields = clearChildrenParentValue({ fields: prevFields });
            set(draft.builderFields, `${row.dataPath}.value.fields`, fields);
          }
        }
        if (
          key === 'value.selected' &&
          [VALUE_FROM.CONTEXT, VALUE_FROM.SELF].includes(get(record, 'value.from'))
        ) {
          const prevFields = get(record, 'value.fields', []);
          if (prevFields.length) {
            const fields = clearChildrenParentValue({
              fields: prevFields,
              shouldChangeFrom: false,
            });
            set(draft.builderFields, `${row.dataPath}.value.fields`, fields);
          }
        }
        set(draft.builderFields, `${row.dataPath}.${key}`, value);
      });
    },
    [update]
  );
  const handleRowRemove = React.useCallback(
    (row) => {
      update((draft) => {
        set(draft.builderFields, `${row.dataPath}.isRemoved`, true);
      });
      // setBuilderFields((fields) => {
      //   return fields.filter((r, i) => rowIndex !== i);
      // });
    },
    [update]
  );

  const handleSubFieldAdd = React.useCallback(
    async (item, expand) => {
      // const oldFields = [...draft.builderFields];
      if (expand) {
        const target = item.target || getCustomTarget(item);
        let fields = modelFieldMap[target];
        const builder = JSON.parse(
          builderRecord[params.resultMetaField] || '{}'
        );
        const builderJSONFields = get(builder, 'fields', []);
        if (!modelFieldMap[target]) {
          if (model.modelType === ModelType.CUSTOM) {
            fields = await fetchCustomFields({ ...item });
          } else {
            fields = await fetchFields(getFieldInfo(item));
          }
          setModelFieldMap({ [target]: fields });
          const dataPath = `${item.dataPath}.value.fields`;
          const newFields = [];

          const builderField = findFields(item, builderJSONFields);
          const jsonFieldList = get(builderField, 'value.fields', []);

          excludeFields(fields).forEach((field, i) => {
            const jsonField =
              jsonFieldList.find((f) => f.name === field.name) || {};
            const valuePath = `${dataPath}[${i}].value`;
            const value = getFieldValue(jsonField.value);
            const from = get(jsonField,'value.from');

            const contextValue = getContextValue(value, from);

            newFields.push({
              ...field,
              dataPath: `${dataPath}[${i}]`,
              from,
              value,
              hideFields: true,
              isRemoved: false,
              modelSubField: jsonField.modelSubField,
              ...contextValue,
            });
          });
          update((draft) => {
            set(
              draft.builderFields,
              `${item.dataPath}.value.fields`,
              newFields
            );
            set(draft.builderFields, `${item.dataPath}.hideFields`, false);
          });
          // const newFields = updateBuilderFields(oldFields, item, excludeFields(fields));
          // setBuilderFields([...newFields]);
        } else {
          const newFields = [];
          const dataPath = `${item.dataPath}.value.fields`;
          const builderField = findFields(item, builderJSONFields);
          const jsonFieldList = get(builderField, 'value.fields', []);
          excludeFields(fields).forEach((field, i) => {
            const jsonField =
              jsonFieldList.find((f) => f.name === field.name) || {};
            const valuePath = `${dataPath}[${i}].value`;
            const value = getFieldValue(jsonField.value);
            const from = get(jsonField, 'value.from');
            const contextValue = getContextValue(value, from);
            newFields.push({
              ...field,
              dataPath: `${dataPath}[${i}]`,
              from,
              value,
              hideFields: true,
              isRemoved: false,
              modelSubField: jsonField.modelSubField,
              ...contextValue,
            });
          });
          update((draft) => {
            set(
              draft.builderFields,
              `${item.dataPath}.value.fields`,
              newFields
            );
            set(draft.builderFields, `${item.dataPath}.hideFields`, false);
          });
          // const newFields = showHideBuilderFields(oldFields, item, false);
          // setBuilderFields([...newFields]);
        }
      } else {
        update((draft) => {
          set(draft.builderFields, `${item.dataPath}.hideFields`, true);
        });
        // hide fields from builder field
        // const newFields = showHideBuilderFields(oldFields, item, true);
        // setBuilderFields([...newFields]);
      }
    },
    [model, modelFieldMap, update, builderRecord, params]
  );

  const handleAdd = React.useCallback(
    (rows) => {
      update((draft) => {
        rows.forEach((row) => {
          set(draft.builderFields, `${row.dataPath}.isRemoved`, false);
        });
      });
    },
    [update]
  );

  const handleSelectAll = React.useCallback(
    (e) => {
      setBuilderFields((fields) => {
        return [
          ...metaFields.map((field) => {
            const index = fields.findIndex((f) => f.name === field.name);
            if (index !== -1) {
              return fields[index];
            }
            return field;
          }),
        ];
      });
    },
    [metaFields, setBuilderFields]
  );

  const getBuilderFields = React.useCallback(
    async (data, _builderRecord = {}) => {
      const newFields = [];
      const builder = JSON.parse(
        _builderRecord[params.resultMetaField] || '{}'
      );
      const jsonData = get(builder, 'fields', []);
      excludeFields([...data]).forEach((field, index) => {
        const jsonFieldIndex = jsonData.findIndex((f) => f.name === field.name);
        const jsonField = jsonData[jsonFieldIndex] || {};

        const valuePath = `${field.name}.value`;
        const value = getFieldValue(jsonField.value);
        const from = get(jsonField, 'value.from');
        // get(jsonData, `${field.name}.from`);
        const contextValue = getContextValue(value, from);
        newFields.push({
          ...field,
          path: generatePath(field),
          from,
          dataPath: `[${index}]`,
          value,
          hideFields: true,
          isRemoved: false,
          modelSubField: getModelSubField(jsonField.modelSubField),
          selfField: jsonField.selfField,
          sourceField: jsonField.sourceField,
          ...contextValue,
        });
      });
      // const builder = JSON.parse(_builderRecord[params.resultMetaField] || '{}');
      // const newFields = getAssignmentFields(jsonData, fields);
      update((draft) => {
        set(draft, 'builderFields', newFields);
      });
      // setBuilderFields(newFields);
    },
    [params, update]
  );

  const getMetaFields = React.useCallback(
    async (model, _builderRecord) => {
      if (model) {
        const data = await fetchFields(model);
        setMetaFields([...data]);
        setErrors({});
        getBuilderFields(data, _builderRecord);
      }
    },
    [getBuilderFields]
  );

  const handleClearError = React.useCallback((path) => {
    setErrors((err) => {
      return JSON.parse(JSON.stringify(set(err, path, undefined)));
    });
  }, []);

  const handleSave = React.useCallback(async () => {
    setErrors({});
    const currentJson = JSON.parse(
      builderRecord[params.resultMetaField] || '{}'
    );
    const jsonFields = generateJson(builderFields, currentJson, getDefaultFrom(sourceModel), sourceModel);
    const json = {
      fields: jsonFields,
      targetModel: model.name,
      sourceModel: getSourceModelString(sourceModelList),
      sourceModelList,
      newRecord,
      isJson: model.modelType === ModelType.CUSTOM,
    };
    const jsonQuery = JSON.stringify({...json});
    const scriptString = await generateScriptString(jsonQuery, params.model);
    const expressionQuery = scriptString || '';
    const record = {
      ...builderRecord,
      [params.resultMetaField]: jsonQuery,
      [params.resultField]: expressionQuery,
      [params.targetField]: json.targetModel,
      [params.sourceField]: json.sourceModel,
    };
    // console.log(JSON.stringify(json));
    // console.log(json, record);
    setLoading(true);
    const result = await saveRecord(params, record);
    setLoading(false);
    if (result) {
      setShowSaveMessage({
        message: 'Builder data saved successfully',
        severity: 'success',
      });
      setBuilderRecord({ ...result });
    } else {
      setShowSaveMessage({
        message: 'Failed to save builder data',
        severity: 'error',
      });
    }
  }, [builderFields, params, model, builderRecord, sourceModel, newRecord, sourceModelList]);

  const handleModelSelect = React.useCallback(
    async (e) => {
      setModel(e);
      setShowSaveMessage(null);
      setBuilderRecord((brec) => {
        return {
          ...brec,
          [params.resultMetaField]: null,
          [params.resultField]: '',
        };
      });
      getMetaFields(e);
    },
    [getMetaFields, params]
  );

  const handleSourceModelSelect = React.useCallback(
    (e) => {
      if (e === null) {
        const fields = clearChildrenParentValue({
          fields: builderFields,
          from: VALUE_FROM.SOURCE,
          shouldChangeFrom: true,
        });
        setBuilderFields([...fields]);
      } else {
        const fields = clearChildrenParentValue({
          fields: builderFields,
          from: VALUE_FROM.SOURCE,
          shouldChangeFrom: false,
        });
        setBuilderFields([...fields]);
      }
      setSourceModel([e]);
    },
    [builderFields, setBuilderFields]
  );

  const handleFieldSelectorOpen = React.useCallback((e) => {
    setOpenFieldSelector(e.currentTarget);
  }, []);

  const handleFieldSelectorClose = React.useCallback(() => {
    setOpenFieldSelector(null);
  }, []);

  const closeSaveMessage = React.useCallback(
    () => setShowSaveMessage(null),
    []
  );

  const searchField = React.useCallback((searchText) => {
    const fields = getNewBuilderFields(builderFields, searchText);
    setBuilderFields([...fields]);
  }, [builderFields, setBuilderFields]);


  const delayFetch = useDebounce(searchField, 400);

  const handleFieldSearch = React.useCallback((e) => {
    const searchText = e.target.value;
    delayFetch(searchText);
  }, [delayFetch]);

  // React.useEffect(() => {
  //   getMetaFields();
  // }, [getMetaFields]);

  // React.useEffect(() => {
  //   // async function fetchBuilderRecord() {
  //   //   if (builderRecord) {
  //   //     const json = JSON.parse(builderRecord[params.resultMetaField] || '{}');
  //   //     const result = await fetchModelByName(json.model);
  //   //     setModel(result);
  //   //   }
  //   // }
  //   // fetchBuilderRecord();
  // }, [builderRecord, params]);

  React.useEffect(() => {
    async function init() {
      if (params && params.id) {
        const result = await fetchRecord(params.model, params.id);
        setBuilderRecord({ ...result });
        if (result) {
          const jsonData = JSON.parse(result[params.resultMetaField] || '{}');
          if (jsonData) {
            const modelResult = await fetchModelByName(
              jsonData.targetModel
            );
            const lastSourceModel = jsonData.sourceModelList ? jsonData.sourceModelList[jsonData.sourceModelList.length - 1] : {};

            const sourceModelResult = await fetchModelByName(
              lastSourceModel.name
            );
            setSourceModelList([...jsonData.sourceModelList]);
            setModel(modelResult);
            setSourceModel(sourceModelResult);
            setNewRecord(jsonData.newRecord);
            getMetaFields(modelResult, result);
          }
        } else {
          setShowSaveMessage({
            message: 'Record is not available',
            severity: 'error',
          });
        }
      }
    }
    init();
  }, [params, getMetaFields]);
  // console.log(errors);
  return (
    <>
    <Grid container>
      <Grid container className={classes.topView}>
        <Grid container justify="space-between" style={{ marginBottom: 10 }}>
          <Selection
            className={classes.input}
            name="metaModal"
            title="Target Model"
            placeholder="Target Model"
            fetchAPI={(e) => getMetaModels(e)}
            optionLabelKey="name"
            onChange={(e) => handleModelSelect(e)}
            value={model}
          />
          <TextField 
            classes={{root: classes.searchField}}
            placeholder="Search fields"
            onChange={(e) => handleFieldSearch(e)}
          />
          <Grid item className={classes.newRecordInputView}>
            <Switch
              className={classes.newRecordSwitch}
              checked={newRecord}
              onChange={(e) => setNewRecord(e.target.checked)}
            />
            <Typography className={classes.newRecordSwitchText}>
              New Record
            </Typography>
          </Grid>
          <Grid item className={classes.metaFieldGrid}>
            <Grid container>
              <FieldPopover
                data={builderFields}
                onSubmit={(data) => handleAdd(data)}
                icon={<AddIcon />}
                buttonTitle="Add Fields"
                iconButton={true}
                buttonClassName={classes.modelFieldSelection}
                iconButtonClassName={classes.addModelFieldIcon}
              />
            <IconButton classes={{colorPrimary: classes.saveIcon}} color="primary" onClick={handleSave} className={classNames(classes.iconButtonClassName)}>
              <SaveIcon />
            </IconButton>
            </Grid>
          </Grid>
        </Grid>
        <MultiSelector 
            containerClassName={classes.selectContainer}
            title="Source Model"
            optionValueKey="name"
            optionLabelKey="name"
            concatValue={true}
            isM2o={true}
            isContext={true}
            value={sourceModelList}
            onChange={(e) => {
              setSourceModelList([...e]);
              setSourceModel(e[e.length - 1]);
            }}
          />
      </Grid>
      <DataTable
        builderFields={builderFields}
        data={builderFields}
        onRowChange={handleRowChange}
        onRemove={handleRowRemove}
        metaFields={metaFields}
        errors={errors}
        onClearError={handleClearError}
        onSubFieldAdd={handleSubFieldAdd}
        handleAdd={handleAdd}
        sourceModel={sourceModel}
        targetModel={model}
      />
    </Grid>
      {loading && <div className={classes.loaderView}>
        <CircularProgress size={32} />
      </div>}
    </>
  );
}

export default Builder;
